@import "./global.css";

/* I look it in the Figma Design and they use everywhere Open Sans.
I import it to use it in all the APP*/

@import url("https://fonts.googleapis.com/css?family=Open+Sans");

ul {
  margin: 0;
  padding: 0;
}
body {
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: max-content;
}

code {
  font-family: "Open Sans", sans-serif;
}

main {
  position: relative;
}
